.header {
  height: 60px;
  display: flex;
  align-items: center;
  background-color: #131921;
  position: sticky;
  top: 0;
  z-index: 100;
}

.header_logo {
  width: 100px;
  object-fit: contain;
  margin: 0 20px;
  margin-top: 18px;
}

.header_search {
  display: flex;
  flex: 1;
  align-items: center;
  border-radius: 24px;
}

.header_searchInput {
  height: 40px;
  padding: 10px;
  border: none;
  width: 100%;
}

.header_searchIcon {
  padding: 5px;
  height: 40px !important;
  background-image: linear-gradient(#faecc8, #f0c250);
  font-size: 30px;
}

.header_nav {
  display: flex;
  justify-content: space-evenly;
}

.header_item {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  padding: 5px;
  color: #fff;
}

.header_item:hover,
.header_basket:hover {
  border: 1px solid #fff;
}
.header_item1 {
  font-size: 10px;
}

.header_item2 {
  font-size: 13px;
  font-weight: 800;
}

.header_basket {
  display: flex;
  padding: 5px;
  align-items: center;
  color: #fff;
}

.header_basketCount {
  margin-left: 10px;
  margin-right: 10px;
}
